<template>
  <div id="app">
    <!-- header -->
    <app-header :poiInfo="poiInfo"></app-header>
    <!-- navigation -->
    <app-nav :commentNum="commentNum"></app-nav>
    <!-- content -->
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import Header from "./components/header/Header";
import Nav from "@/components/nav/Nav";
export default {
  name: "App",
  data() {
    return {
      poiInfo: {},
      commentNum: 0
    };
  },
  components: {
    "app-header": Header,
    "app-nav": Nav
  },
  created() {
    fetch("https://meituan-6f7c4.firebaseio.com/goods.json")
      .then(res => {
        return res.json();
      })
      .then(response => {
        console.log(response)
        for (const key in response) {
          var element = response[key];
        }
        if(element){
          this.poiInfo = element.poi_info
        }
      });

    fetch("https://meituan-6f7c4.firebaseio.com/ratings.json")
      .then(res => {
        return res.json();
      })
      .then(response => {
        for (const key in response) {
          var element = response[key];
        }
        if(element){
          this.commentNum = element.comment_num
          // console.log(this.commentNum)
        }
      });
  }
};
</script>

<style>
</style>



